.tranForm{
   
    background-color: aliceblue;
    padding:10px;
    border-radius: 4px;
    width: 80%;
    margin: 0 auto;
    text-align: center;
    font-size: 20px;
    

background-color: #171f34;
border-radius: 10px;
color:white;
-webkit-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
-moz-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
font-weight: 500;
}

.tranForm input{
    margin: 18px;
    width: 90%;
    font-size: 18px;
    height: 30px;
    outline: none;
    border: none;
    border-radius: 6px;
    -webkit-box-shadow: 8px 8px 24px -14px rgba(66, 68, 90, 1);
-moz-box-shadow: 8px 8px 24px -14px rgba(66, 68, 90, 1);
box-shadow: 8px 8px 24px -14px rgba(66, 68, 90, 1);

}


.tranForm button{
    padding:10px 15px;
    margin: 6px;
    color:white;
    background-color: #3a3c6c;
    border: none;
    border-radius: 5px;

}