.heading li{
    list-style: none;
}

.heading img{
    width: 50px;
    border-top: 6px solid snow;
    border-bottom: 6px solid snow;
    border-radius: 50%;
    height: 50px;
}

.heading a{
    text-decoration: none;
    color:black;
    font-size:18px;
}

.heading{
    border-bottom-left-radius: 70px;
    border-top-right-radius: 70px;
    -webkit-box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
-moz-box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
    display: flex;
    flex-direction: row;
    width: 70%;
    margin:0 auto;
    align-items: center;
    justify-content: space-around;
}

.heading button{
    padding:8px 12px;
    color:snow;
    border-radius: 4px;
    border:none;
    background-color: #f73d3d;
   
}

.navbar ul{
    display: flex;
    
}

.navbar ul li{
    padding: 6px;
    margin: 0px 6px;
    border: 2px solid white;
    border-radius: 4px;
}

.heading .info{
    display: flex;
    flex-direction: row;
    gap:20px;
    align-items: center;
    background-color: #121824;
    border-top-left-radius: 10px;
    padding: 8px 14px;
    color:snow;
    border-top-right-radius: 10px;
border-bottom: 2px solid white;
    -webkit-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
-moz-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
}


.heading .info p{
    font-size: 22px;
}

