.loginForm{
    position: absolute;
    left:35%;
    top:20%;
    width: 30%;
    margin:0 auto;
    color:snow;
    text-align: center;
    border-radius: 10px;
 background-image: url('../../img/winter1.jpg');

 font-size: 18px;
    padding:15px 15px 0px 15px;
    -webkit-box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
-moz-box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
box-shadow: 8px 8px 24px -8px rgba(66, 68, 90, 1);
}

.loginForm form{
    background-color: #121824;
    padding:10px;
    text-align: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    color:azure;
    opacity: 1;   
}

.loginForm h2{
    background-color: #121824;
    padding:8px;
    opacity: 0.95;   
    border:2px solid snow;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.loginForm input{
   margin: 14px 0px;
   width: 70%;
   color:snow;
   font-size: 18px;
   padding:4px;
   height: 24px;
   border:1px solid snow;
   outline:none;
   background: none;
}

.loginForm button{
   color: #f8fbff;
   border: none;
   font-size: 14px;
   border-radius: 4px;
   padding:8px 20px;
   margin:10px;
   background-color: rgb(247, 61, 61);
}