.cost{
    font-weight: 400;
    font-size: 22px;
}

.name{
    font-weight: bolder;
    font-size: 22px;
}

.li_inner{
    display: grid;
    grid-template-columns: 2fr auto;
    padding: 10px;
    background-color: #171f34;
    border-radius: 10px;
    color:white;
    -webkit-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
-moz-box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
box-shadow: 8px 8px 24px -9px rgba(0, 0, 0, 1);
}

.deletebtn{
    color: rgb(255, 255, 255);
    background-color: #f73d3d;
    border:none;
    padding:4px 6px;
    font-weight: bolder;
    border-radius: 2px;

}